/**
 * The Flow Segment Data service provides information about the speeds and travel times of the road
 * fragment closest to the given coordinates.
 *
 * @class trafficFlowSegmentData
 * @namespace Services.services
 * @module Services
 * @uses GoMixin
 * @uses KeyMixin
 * @param {Object} options Options to be passed to the flowSegmentData call.
 * @param {String} options.point Coordinates of the point close to the road segment.
 * @param {String} options.key Traffic Flow API Key that can be obtained from
 *     <a href="DEV_PORTAL_BASE_URL">TomTom's Developer Portal</a>.
 * @param {String} options.style The type of flow data to be displayed on the tile. Available options: absolute,
 *     relative, relative-delay.
 * @param {Number} options.zoom Zoom level. This will affect traffic incident coordinates to separately present both
 *     road sides.
 * @param {String} [options.unit=KMPH] Unit of speed. Available options: KMPH, MPH.
 * @param {Number} [options.thickness=10] Segment width multiplier. Accepts values in the range from 0 to 20.
 * @constructor
 */

import {SERVICE_TYPES} from 'Core/serviceTypes';
import validators from './validators';
import converters from './converters';
import { flowSegmentDataRest } from 'rest/flowSegmentData';
import flowSegmentDataModel from '../model/traffic/flowSegmentData';
import parameterApplications from '../common/parameterApplications';
import serviceFactory from './serviceFactory';

const fields = {
    key: {
        validators: [validators.string]
    },

    /**
     * @attribute zoom
     * @param {Integer} zoom Zoom level. This will affect traffic incident coordinates to separately present both road
     * sides. Should be in the range from 0 to 18.
     * @throws {TypeError} If the given argument cannot be converted into a valid value.
     */
    zoom: {
        validators: [validators.zoomLevel],
        required: true,
        application: parameterApplications.PATH
    },

    /**
     * @attribute style
     * @param {String} style The type of flow data to be displayed on the tile. Available options: absolute,
     * relative, relative-delay.
     * @throws {TypeError} If the given argument cannot be converted into a valid value.
     */
    style: {
        validators: [validators.oneOfValue(['absolute', 'relative', 'relative-delay'], 'traffic flow style')],
        required: true,
        application: parameterApplications.PATH
    },

    /**
     * @attribute point
     * @param {String} point Coordinates of the point close to the road segment.
     * They have to be comma-separated and calculated using the EPSG4326 projection.
     * @throws {TypeError} If the given argument cannot be converted into a valid value.
     */
    point: {
        converters: [converters.point],
        validators: [validators.point],
        required: true
    },

    /**
     * @attribute unit
     * @param {String} unit Unit of speed. Avilable values: KMPH, MPH.
     * @throws {TypeError} If the given argument cannot be converted into a valid value.
     */
    unit: {
        validators: [validators.oneOfValue(['KMPH', 'MPH'])]
    },

    /**
     * @attribute thickness
     * @param {Integer} thickness Segment width multiplier. Value should be in the range from 1 to 20.
     * @throws {TypeError} If the given argument cannot be converted into a valid value.
     */
    thickness: {
        validators: [validators.numberInInterval(1, 20)]
    },

    /**
     * @attribute openLr
     * @param {Boolean} openLr Specifies if the response includes OpenLR code.
     * @throws {TypeError} If the given argument cannot be converted into a valid value.
     */
    openLr: {
        validators: [validators.bool]
    }
};

const handleServiceCall = (data) => {
    return flowSegmentDataRest(fields, data).then(response => flowSegmentDataModel(response));
};

export const trafficFlowSegmentData =
    serviceFactory(fields, SERVICE_TYPES.TRAFFIC_FLOW, 'trafficFlowSegmentData', handleServiceCall);



